.container-platform {
  width: 1920px;
  position: relative;
}

.default {
  width: 1920px;
  height: 1080px;
  display: block;
}

.floating-menu {
  position: absolute;
  bottom: 10px;
  right: 10px;

  .menu .expand {
    display: inline;
  }

  .menu .collapse {
    display: none;
  }

  .menu {
    background-color: #191a1c;
    border-style: solid;
    border-width: 1px;
    border-color: #555555;

    -webkit-box-shadow: 5px 5px 15px 5px rgba(0, 0, 0, 0.37);
    box-shadow: 5px 5px 15px 5px rgba(0, 0, 0, 0.37);

    .list-item {
      padding-left: 25px;
      padding-right: 25px;

      .item {
        font-family: "Teko";
        font-size: 25px;
        color: #b0b0b0;
      }
      .item:hover {
        color: #3c99d0;
        text-decoration: underline;
        cursor: pointer;
      }
    }
  }
  .main-button {
    float: right;
    height: 40px;
    width: 40px;
    background-color: #191a1c;
    border-radius: 50%;

    -webkit-box-shadow: 5px 5px 15px 5px rgba(0, 0, 0, 0.37);
    box-shadow: 5px 5px 15px 5px rgba(0, 0, 0, 0.37);

    .C4-icon-expand {
      margin-top: 15px;
      margin-left: 15px;
    }
    .C4-icon-hide {
      margin-top: 15px;
      margin-left: 15px;
      transform: rotate(180deg);
    }
  }
}
.arrow-up {
  background: url("Assets/arrow-up.svg") no-repeat;
  width: 12px;
  height: 8px;
  margin: auto;
  float: left;
  cursor: pointer;
}

.list-container {
  display: flex;
  flex-direction: column;
  .list-item {
    padding: 8px 0px 8px 8px;
    color: #b0b0b0;
    font-weight: 400;

    .value {
      font-family: Teko;

      font-size: 21px;
      line-height: 14px;
      font-weight: 500;
    }
  }

  .list-item:nth-child(odd) {
    background-color: #373737;
  }
}
